.page {
  min-height: 100%;
}

.pageHeader {
  border-bottom: solid 1px #ddd;
  background: #fff;
  z-index: 100;
}

.pageContent {
  width: 100%;
  min-height: 500px;
}

.consoleControl {
  padding: 15px 20px;
  background-color: #333;
  color: rgba(255, 255, 255, 0.9);
  position: sticky;
  top: 0;
  z-index: 100;
}

.console {
  position: sticky !important;
  bottom: 0 !important;
  border-top: 3px solid #aaa;
}

.consoleInner {
  width: 100%;
  height: 100%;
  padding: 5px 0 15px;
  overflow-y: auto;
  background-color: #333;
}
