.page {
  width: 100%;
  height: 100%;
  min-height: 1000px;
  padding: 30px;
  position: relative;
}
.form {
  width: 300px;
  position: absolute;
  top: 20px;
  left: 20px;
}

.openButton {
  position: absolute;
  top: 300px;
  left: 20px;
  transform: translateX(0) translateY(0);
  cursor: move;
  z-index: 1000;
}

.dot {
  width: 6px;
  height: 6px;
  position: absolute;
  top: -3px;
  left: -3px;
  background: red;
  border-radius: 50%;
}
